.transparentScreen {
  position: fixed;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  left: 0;
  top: 0;
}

.panel {
  position: fixed;
  user-select: none;
  background-color: #262626;
  border-radius: 1vw;
  padding: 2vw;
  box-shadow: 0 0 2vw 0.5vw #22222266;
  color: #aaaaaa;
  .choice {
    display: flex;
    align-items: center;
    padding: 0.5vw 2vw 0.5vw 1vw;
    border-radius: 4px;
    &:hover {
      color: #dddddd;
      cursor: pointer;
      background-color: #237e54;
    }
    .icon {
      display: block;
      width: 4vw;
      height: 4vw;
      margin-right: 2vw;
    }
  }
}
