.noBlur {
  filter: blur(0);
}
.blur {
  filter: blur(1px);
}
.shadow {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background-color: #22222288;
}
.promptFrame {
  user-select: none;
  position: fixed;
  width: 100vw;
  height: 88vh;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  .prompt {
    width: 100vw;
    padding: 10px 0;
    background-color: #55555566;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #cccccc;
    .title,
    .text {
      margin-bottom: 8px;
      max-width: 400px;
    }
    .text {
      color: #aaaaaa;
    }
    .content {
      overflow-wrap: break-word;
      background-color: #262626;
      color: #aaaaaa;
      width: fit-content;
      border-radius: 4px;
      padding: 4px 10px;
      max-width: 320px;
      margin-bottom: 8px;
    }
    .buttons {
      display: flex;

      .button {
        margin: 0 8px;
        padding: 0 4px;
        border-radius: 4px;
        &:hover {
          color: #eeeeee;
          background-color: #222222cc;
          cursor: pointer;
        }
      }
    }
  }
}
