.frame {
  user-select: none;
  display: flex;
  flex-direction: column;
  width: 100vw;
  align-items: center;
  margin-top: 24vh;
  .title {
    color: #cccccc;
    font-size: 80px;
    font-weight: bold;
    border-bottom: 2px #cccccc solid;
    width: fit-content;
    padding: 0 200px 12px;
    margin-bottom: 24px;
    animation-name: light;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
  }
  .panel {
    display: flex;
    font-size: 20px;
    width: 540px;
    height: 80px;
    align-items: baseline;
    justify-content: space-between;
    .left {
      .prompt {
        margin-bottom: 10px;
        .link {
          color: #888888;
          text-decoration: none;
          &:hover {
            color: #666666;
          }
        }
      }
      .urlPrompt {
        margin-bottom: 10px;
      }
      .url {
        display: flex;
        align-items: baseline;
        .protocol {
          background-color: #333333;
          padding: 0 6px;
          &:hover {
            cursor: pointer;
          }
        }
        .textarea {
          width: 240px;
          height: 200px;
          color: #bbbbbb;
          padding: 0;
          margin: 0;
          border: none;
          font-size: 20px;
          font-family: "Segoe UI", "Microsoft YaHei UI", sans-serif;
          background-color: transparent;
          outline: none;
          resize: none;
          &::selection {
            background-color: #cccccc;
            color: #222222;
          }
        }
      }
    }

    .buttons {
      display: flex;
      .yes,
      .no,
      .ok,
      .cancel {
        padding: 0 2px;
        margin-left: 20px;
        height: fit-content;
        &:hover {
          background-color: #dddddd;
          color: #222222;
          cursor: pointer;
        }
      }
    }
  }
}
@keyframes light {
  0% {
    color: #999999;
  }
  80% {
    color: #cccccc;
  }
  100% {
    color: #999999;
  }
}
