.oustr {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 100vw;
  height: 40vh;

  font-weight: bold;
  font-size: 16vw;
  user-select: none;
  position: relative;

  .githubIo {
    position: absolute;
    font-weight: normal;
    font-size: 5vw;
    transform: translate(16vw, 3vw);
  }
}

.buttons {
  user-select: none;
  margin-top: 8vw;
  display: flex;
  justify-content: center;
  .button {
    text-decoration: none;
    display: inline-block;
    background-color: #237e54;
    color: #cccccc;
    font-size: 5vw;
    border-radius: 1vw;
    width: fit-content;
    padding: 0 2vw;
    height: 8vw;
    line-height: 8vw;
    &:active {
      background-color: #146542;
      color: #888888;
    }
    .buttonIcon {
      display: inline-block;
      width: 4vw;
      height: 4vw;
      margin-right: 2vw;
    }
  }
}

.footer {
  user-select: none;
  text-align: center;
  width: 100vw;
  font-size: 4.4vw;
  position: fixed;
  bottom: 8vw;
  color: #888888;
  .githubLink {
    text-decoration: none;
    display: inline-block;
    background-color: #237e54;
    color: #cccccc;
    border-radius: 1vw;
    width: fit-content;
    padding: 0 2vw;
    height: 6vw;
    line-height: 6vw;
    &:active {
      background-color: #146542;
      color: #888888;
    }
  }
}
