.root {
  //overflow-x: hidden;
  width: 100vw;
  height: 100vh;
  font-size: 4vw;
}

.mobileLayout {
  width: 100vw;
  //height: 100vh;
  display: flex;
  flex-direction: column;
  .fileBar {
    box-sizing: border-box;
    background-color: #262626;
    padding: 0 4vw;
    height: 12vw; // 上下padding分别为2vw
    display: flex;
    width: 100vw;
    align-items: center;

    .fileInput {
      display: none;
    }
    .textTitle {
      flex: 1;
      margin-right: 2vw;
      user-select: none;
      &:active {
        color: #666666;
      }
    }

    .leftButtons {
      flex: 1;
      display: flex;
      .importButton,
      .newButton {
        user-select: none;
        box-sizing: border-box;
        background-color: #237e54;
        border-radius: 1vw;
        width: fit-content;
        color: #cccccc;
        padding: 0 2vw;
        height: 8vw;
        line-height: 8vw;
        margin-right: 2vw;

        &:active {
          background-color: #146542;
          color: #888888;
          cursor: pointer;
        }
      }
    }
    .saveButton {
      box-sizing: border-box;
      background-color: #237e54;
      border-radius: 1vw;
      width: fit-content;
      color: #cccccc;
      padding: 0 2vw;
      height: 8vw;
      line-height: 8vw;
      user-select: none;

      &:active {
        background-color: #146542;
        color: #888888;
        cursor: pointer;
      }

      &.disabled {
        background-color: #333333;
        color: #666666;
      }
    }
  }

  .segsFrame {
    //height: 0;
    height: calc(100% - 46vw);
    flex: 1;
    width: 100vw;

    .noTextBackground {
      width: 100%;
      height: 100%;
      user-select: none;
      .noText {
        box-sizing: border-box;
        width: 100%;
        height: 64%;
        padding: 4vw;
        font-weight: bold;
        color: #444444;
        font-size: 8vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .howToUse {
          position: absolute;
          font-size: 4vw;
          font-weight: normal;
          transform: translateY(14vw);
          box-sizing: border-box;
          width: fit-content;
          height: 8vw;
          text-align: center;
          line-height: 8vw;
          padding: 0 2vw;
          border-radius: 1vw;
          background-color: #282828;
          color: #555555;
          &:active {
            background-color: transparent;
          }
        }
      }
    }

    .segsContent {
      overflow-y: scroll;
      box-sizing: border-box;
      //height: calc(100vh - 46vw);
      height: 100%;

      width: 100vw;
      padding: 4vw;
      //padding-bottom: calc(24 / 100 * (100vh - 44vw));
      padding-bottom: 24%;
      display: flex;
      flex-direction: column;

      div::selection {
        background-color: #2e8b61;
        color: #222222;
      }
    }
    .topShade,
    .bottomShade {
      position: fixed;
      left: 0;
      width: 100vw;
      height: 1vw;
      background-color: #237e54;
      animation-name: disappear;
      animation-duration: 0.4s;
      animation-fill-mode: forwards;
    }
    .topShade {
      top: 12vw;
    }
    .bottomShade {
      bottom: 34vw;
    }
    @keyframes disappear {
      0% {
        opacity: 1;
      }
      100% {
        opacity: 0;
        height: 0;
        display: none;
      }
    }
  }
  .textPanel {
    height: 34vw; // 4vw (上下padding) + 20vw + 2vw + 8vw
    width: 100vw;
    background-color: #262626;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .segTextInputPanel {
      box-sizing: border-box;
      width: 100vw;

      padding: 0 4vw;

      display: flex;
      flex-direction: column;

      .segTextInput {
        box-sizing: border-box;
        width: 100%;
        height: 20vw;
        font-size: inherit;
        font-family: inherit;
        resize: none;
        outline: none;
        padding: 2vw;
        background-color: #222222;
        color: #bbbbbb;
        border: 0.5vw #444444 solid;
        border-radius: 1vw;
        margin-bottom: 2vw;

        &::selection {
          background-color: #2e8b61;
          color: #222222;
        }
        &:focus {
          border-color: #666666;
        }
      }
      .buttons {
        display: flex;
        justify-content: space-between;
        user-select: none;
        .leftButtons {
          display: flex;
          .whichSegToSendButton,
          .nextLineToggleButton {
            box-sizing: border-box;
            width: 8vw;
            height: 8vw;
            margin-right: 2vw;
            text-align: center;
            line-height: 8vw;
            border-radius: 1vw;
            background-color: #333333;
            color: #666666;

            &.selected {
              background-color: #666666;
              color: #cccccc;
              &:active {
                background-color: #666666;
                color: #cccccc;
                cursor: default;
              }
            }
            &:active {
              background-color: #444444;
              color: #888888;
              cursor: pointer;
            }
          }
          .nextLineToggleButton {
            width: fit-content;
            padding: 0 2vw;
          }
        }
        .sendButton {
          box-sizing: border-box;
          background-color: #237e54;
          border-radius: 1vw;
          height: 8vw;
          width: 12vw;
          line-height: 8vw;
          text-align: center;
          color: #cccccc;
          align-self: end;
          font-size: 4.5vw;

          &:active {
            background-color: #146542;
            color: #888888;
            cursor: pointer;
          }

          &.disabled {
            background-color: #333333;
            color: #666666;
          }
        }
      }
    }
  }
}

.toTopOrBottomButton {
  position: fixed;
  user-select: none;
  background-color: #282828;
  border-radius: 1vw;
  width: 12vw;
  height: 12vw;
  color: #666666;
  line-height: 12vw;
  text-align: center;
  bottom: 38vw;
  font-size: 5vw;
  right: 4vw;
  &:active {
    background-color: #333333;
    color: #237e54;
    cursor: pointer;
  }
}
