.background {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
.panel {
  position: fixed;
  top: calc(30vh - 20vw);
  left: 6vw;
  box-sizing: border-box;
  width: 88vw;
  height: fit-content;
  user-select: none;
  background-color: #262626;
  border-radius: 1vw;
  padding: 4vw;
  box-shadow: 0 0 2vw 0.5vw #22222266;
  color: #aaaaaa;
  .titleSetting {
    display: flex;
    align-items: center;
    margin-bottom: 4vw;
    .label {
      margin-right: 8vw;
    }
    .titleInput {
      flex: 1;
      //margin: 0 auto;
      //width: 60vw;
      box-sizing: border-box;

      font-size: inherit;
      font-family: inherit;
      resize: none;
      outline: none;
      padding: 2vw;
      background-color: #222222;
      color: #bbbbbb;
      border: 0.5vw #444444 solid;
      border-radius: 1vw;

      &::selection {
        background-color: #2e8b61;
        color: #222222;
      }
      &:focus {
        border-color: #666666;
      }
    }
  }
  .fileNameFormatSetting {
    display: flex;
    align-items: center;
    .label {
      margin-right: 2vw;
    }
    .switchIcon {
      &:active {
        color: #666666;
      }
    }
  }
  .fileNamePreview {
    margin: 3vw 8vw;
    color: #666666;
    overflow-wrap: break-word;
    white-space: pre-wrap;
  }
  .setToTopOrBottom {
    display: flex;
    align-items: center;
    margin-bottom: 4vw;
    .label {
      margin-right: 2vw;
    }
    .toggle {
      box-sizing: border-box;
      width: fit-content;
      height: 6vw;
      line-height: 6vw;
      padding: 0 2vw;
      text-align: center;
      border-radius: 1vw;
      background-color: #333333;
      color: #aaaaaa;
      &:active {
        background-color: #262626;
        color: #888888;
      }
    }
  }
  .buttons {
    display: flex;
    justify-content: space-between;
    .leftButtons {
      display: flex;
      .leave {
        box-sizing: border-box;
        padding: 0 2vw;
        height: 8vw;
        text-align: center;
        line-height: 8vw;
        border-radius: 1vw;
        background-color: #303030;
        // 不然会感觉这个按钮比cancel亮
        color: #aaaaaa;

        &:active {
          background-color: #262626;
          color: #888888;
        }

        &.processing {
          background-color: #262626;
          color: #888888;
        }
      }
    }
    .rightButtons {
      display: flex;
      .ok {
        box-sizing: border-box;
        background-color: #237e54;
        border-radius: 1vw;
        padding: 0 2vw;
        height: 8vw;
        line-height: 8vw;
        text-align: center;
        color: #cccccc;
        align-self: end;
        margin-right: 2vw;

        &:active {
          background-color: #146542;
          color: #888888;
          cursor: pointer;
        }
      }
      .cancel {
        box-sizing: border-box;
        padding: 0 2vw;
        height: 8vw;
        text-align: center;
        line-height: 8vw;
        border-radius: 1vw;
        background-color: #333333;
        color: #aaaaaa;

        &:active {
          background-color: #262626;
          color: #888888;
        }
      }
    }
  }
}
