.transparentScreen {
  position: fixed;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  left: 0;
  top: 0;
}

.panel {
  position: fixed;
  left: 0;
  top: 0;
  user-select: none;
  background-color: #222222;
  border-radius: 4px;
  padding: 8px;
  box-shadow: 0 0 4px 2px #22222266;
  color: #aaaaaa;
  .choice {
    display: flex;
    align-items: center;
    padding: 2px 8px 2px 4px;
    border-radius: 4px;
    background-color: #222222;
    &:hover {
      color: #dddddd;
      cursor: pointer;
      background-color: #237e54;
    }
    .icon {
      display: block;
      width: 16px;
      height: 16px;
      margin-right: 8px;
    }
  }
}
