.centerSeg,
.leftSeg,
.rightSeg {
  overflow-wrap: break-word;
  white-space: pre-wrap;
  user-select: none;
}

.leftSeg,
.rightSeg {
  background-color: #333333;
  color: #bbbbbb;
  width: fit-content;
  box-sizing: border-box;
  padding: 1vw 2vw;
  border-radius: 1vw;
  margin-bottom: 2vw;
  max-width: 72vw;
}
.leftSeg {
  background-color: #bbbbbb;
  color: #222222;
}
.rightSeg {
  align-self: flex-end;
}
.centerSeg {
  align-self: center;
  color: #666666;
  margin-bottom: 2vw;
  max-width: 72vw;
}
.focused {
  background-color: #2e8c5f;
  color: #222222;
  border-radius: 1vw;
  padding-left: 2vw;
  padding-right: 2vw;
}
