$width: 400px;
$padding: 0 8px;

.outer {
  font-size: 8px;
  font-family: "Consolas", monospace;
  background-color: #383838;
  display: inline-block;
  .inner {
    font-size: 16px;
  }
}

.line {
  margin: 100px 0;
  border: 2px #666666 solid;
}

.textareaFrame {
  box-sizing: border-box;
  width: $width;
  display: flex;
  height: fit-content;
  position: relative;

  margin: 0 auto;

  .what {
    box-sizing: border-box;
    width: $width;
    overflow-wrap: break-word;
    padding: $padding;
    z-index: -30;
    visibility: hidden;
    white-space: pre-wrap;

    &.extraLine {
      padding-bottom: 22px;
    }
  }

  .textarea {
    box-sizing: border-box;
    padding: $padding;
    border: none;
    outline: none;
    background-color: #333333;
    width: $width;
    height: 100%;
    min-height: 22px;
    resize: none;
    font-family: inherit;
    color: #eeeeee;
    font-size: inherit;
    overflow-y: hidden;
    z-index: 10;
    position: absolute;
  }
}

.input {
  position: fixed;
  bottom: 0;
}

.editor {
  width: 640px;
  height: 320px;
  background-color: #262626;
  border: none;
  outline: none;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 8px;
}
