.hiddenMonospaceLetter {
  color: transparent;
  position: fixed;
  //display: none;
  font-size: 14px;
  font-family: "Consolas", monospace;
}
.root {
  overflow-x: hidden;
}
.frame {
  width: 720px;
  margin-left: calc((100vw - 720px) / 2 - 128px);
  color: #bbbbbb;
  tab-size: 32px;
  border-left: 2px #444444 solid;
  border-right: 2px #444444 solid;
  min-height: 100vh;
  ::selection {
    color: #222222;
    background-color: #bbbbbb;
  }
  p {
    font-size: 10px; //这个也决定了min-height!
  }
  .fileInput {
    display: none;
  }
  .rightPanel {
    user-select: none;
    box-sizing: border-box;
    width: 280px;
    position: fixed;
    top: 32px;
    left: calc((100vw - 720px) / 2 - 128px + 720px + 32px);
    font-size: 16px;
    .upperPanel {
      margin-bottom: 64px;
      .title {
        font-size: 18px;
        line-height: 20px;
        width: fit-content;
        color: #aaaaaa;
        margin-bottom: 4px;
        &.noTitle {
          color: #888888;
        }
        &:hover {
          color: #e8bf6a;
          cursor: pointer;
        }
      }
      .timeStamps {
        margin-bottom: 16px;
        .latestTimeStamp,
        .lastExportedTimeStamp {
          color: #888888;
          font-size: 14px;
        }
      }

      .buttons {
        display: flex;
        align-items: baseline;
        margin-bottom: 8px;
        .save,
        .choose,
        .export,
        .import,
        .exit {
          width: fit-content;
          color: #aaaaaa;
          //background-color: #333333;
          //padding: 2px 6px;
          margin-right: 12px;
          &:hover {
            cursor: pointer;
            //background-color: #282828;
            color: #666666;
          }
        }
        .first {
          background-color: #be943b;
          box-sizing: border-box;
          padding: 2px 6px;
          color: #222222;
          &:hover {
            background-color: #9b7623;
            color: #222222;
          }
        }
      }
    }
    .lowerPanel {
      user-select: none;
      margin-bottom: 64px;
      .buttonGroup {
        display: flex;
        align-items: baseline;
        margin-bottom: 5px;
        div {
          width: fit-content;
          padding: 2px 6px;
          padding-left: 0;
          margin-right: 8px;
          text-align: center;
          &:hover {
            cursor: pointer;
            color: #e8bf6a;
          }
        }
        .strongButton {
          font-size: 17px;
          font-weight: bold;
          color: #aaaaaa;
        }
        .obliqueButton {
          font-style: italic;
        }
        .underLineButton {
          text-decoration: underline;
        }
        .deleteLineButton {
          text-decoration: line-through;
        }
        .colorButton {
          width: 16px;
          height: 16px;
          border-radius: 8px;
          background-color: #c79d45;
          padding: 0;
          transform: translateY(2px);
          &:hover {
            background-color: #b48c38;
          }
        }
        .codeSpanButton,
        .codeBlockButton {
          font-family: "Consolas", monospace;
          font-size: 14px;
          background-color: #313131;
          padding: 2px 4px;
          box-sizing: border-box;
          margin-top: 8px;
          margin-right: 12px;
        }
        .linkButton {
        }
      }
    }
    .infoPanel {
      user-select: none;
      width: 180px;
      font-size: 14px;
      color: #888888;
      .infoNode {
      }
    }
  }
}

.strong {
  color: #dddddd;
  font-weight: bold;
}
.oblique {
  font-style: italic;
}
// 让underline和line-through不能同时存在
.underLine {
  // 大写Line！和throughLine一样！
  // 这里使用了非常巧妙的方法，那就是underline是在文字下方，当它和背景颜色一样的时候看不出来，但是一旦有::selection就能看出来
  // 而且selection的字体颜色恰好全部都是underline的颜色
  // 注意，firefoxselection的时候没法text-decoration: underline
  border-bottom: 1px solid;
  text-decoration: #222222 underline;
  text-decoration-thickness: 1.5px;
  &.ultraSize {
    border-width: 2px;
    text-decoration-thickness: 2.5px;
  }
}
.throughLine {
  text-decoration: line-through;
}
.color {
  color: #e8bf6a !important;
  ::selection {
    background-color: #c79d45;
  }
}
.codeSpan {
  // 真尼玛服了 inline-block本身要设置font-size尽可能小 孩子的东西撑大都行 但本身的font-size决定了min-height！
  font-family: "Consolas", "Microsoft YaHei UI", monospace;
  background-color: #303030;
  display: inline-block;
  font-size: 0;
  min-height: 2px;
  height: fit-content;
  //border-radius: 4px;
  margin: 0 4px;
  padding: 2px 4px;
  box-sizing: border-box;
  .smallSize {
    font-size: 10px;
  }
  .normalSize {
    font-size: 14px;
  }
  .bigSize {
    font-size: 18px;
  }
  .ultraSize {
    font-size: 22px;
  }
}
.linkSpan {
  text-decoration: none;
  color: #888888;

  display: inline-block;
  font-size: 0;
  min-height: 2px;
  height: fit-content;
  .smallSize,
  .normalSize,
  .bigSize,
  .ultraSize {
    &:hover {
      color: #e8bf6a;
      cursor: pointer;
    }
  }
  .smallSize {
    color: #777777;
    font-size: 12px;
  }
  .normalSize {
    font-size: 16px;
  }
  .bigSize {
    color: #999999;
    font-size: 20px;
  }
  .ultraSize {
    color: #aaaaaa;
    font-size: 24px;
  }

  ::selection {
    background-color: #333333;
    color: #cccccc;
  }
}
.textBlock {
}
.codeBlock {
  background-color: #282828;
  font-family: "Consolas", "Microsoft YaHei UI", monospace;
  width: 100%;
  box-sizing: border-box;
  padding: 4px 10px;
  margin: 6px 0;
  overflow-x: hidden;
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
  .codeLine {
    //width: 100%;
    font-size: 14px;
    word-break: break-all;

    &:hover {
      background-color: #333333;
    }
  }
  //.smallSize,
  //.normalSize,
  //.bigSize,
  //.ultraSize {
  //  font-size: 14px;
  //}
}
.smallSize {
  color: #888888;
  font-size: 12px;
}
.normalSize {
  font-size: 16px;
}
.bigSize {
  color: #dddddd;
  font-size: 20px;
}
.ultraSize {
  font-size: 24px;
  color: #dddddd;
  font-weight: bold;
}

.inlineChromiumBugfix {
  font-size: 0;
}
