.noBlur {
  filter: blur(0);
}
.blur {
  filter: blur(0.2vw);
}
.shadow {
  position: fixed;
  width: 100vw;
  //height: 100vh;
  left: 0;
  top: 0;
  background-color: #22222288;
}
.promptFrame {
  user-select: none;
  position: fixed;
  width: 100vw;
  //height: 88vh;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  .prompt {
    width: 100vw;
    padding: 2vw 0;
    background-color: #333333cc;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #cccccc;
    .title {
      margin-bottom: 2vw;
    }
    .toggles {
      display: flex;
      margin-bottom: 2vw;
      .toggle {
        box-sizing: border-box;
        width: fit-content;
        padding: 0 2vw;
        text-align: center;
        border-radius: 1vw;
        border: 0.5vw solid #222222aa;
        margin: 0 1vw;
        color: #aaaaaa;

        &.current {
          background-color: #222222cc;
          color: #cccccc;
        }
      }
    }
    .textareaFrame {
      box-sizing: border-box;
      width: 72vw;
      display: flex;
      height: fit-content;
      position: relative;

      margin-bottom: 2vw;

      .textareaHidden {
        box-sizing: border-box;
        border: 0.5vw transparent solid;
        width: 72vw;
        max-height: 36vw;
        overflow-wrap: break-word;
        white-space: pre-wrap;
        padding: 2vw;
        z-index: -10;
        visibility: hidden;
      }
      .textarea {
        box-sizing: border-box;
        position: absolute;
        width: 72vw;
        height: 100%;
        max-height: 36vw;
        font-size: inherit;
        font-family: inherit;
        resize: none;
        outline: none;
        padding: 2vw;
        background-color: #11111166;
        color: #aaaaaa;
        border: 0.5vw #444444 solid;
        border-radius: 1vw;

        &::selection {
          background-color: #aaaaaa;
          color: #222222;
        }
        &:focus {
          border-color: #555555;
        }
      }
    }

    .content {
      overflow-wrap: break-word;
      white-space: pre-wrap;
      background-color: #262626;
      color: #aaaaaa;
      width: fit-content;
      border-radius: 1vw;
      padding: 1vw 2vw;
      box-sizing: border-box;
      max-width: 72vw;
      margin-bottom: 2vw;
    }
    .buttons {
      display: flex;

      .button {
        margin: 0 2vw;
        padding: 0 1vw;
        border-radius: 1vw;
        &:active {
          color: #eeeeee;
          background-color: #222222cc;
          cursor: pointer;
        }
      }
    }
  }
}
