.noBlur {
  filter: blur(0);
}
.blur {
  filter: blur(1px);
}
.shadow {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background-color: #11111188;
}
.panelFrame {
  top: 0;
  user-select: none;
  font-size: 16px;
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;

  .panel {
    color: #aaaaaa;
    margin-top: 20vh;
    height: fit-content;
    padding: 24px;
    background-color: #1f1f1f;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .panelTitle {
      font-size: 18px;
      margin-bottom: 16px;
    }
    .buttons {
      margin-top: 20px;
      display: flex;
      align-self: flex-end;
      box-sizing: border-box;
      .cancel,
      .ok {
        margin-left: 16px;
        padding: 2px 6px;
        &:hover {
          cursor: pointer;
          color: #eeeeee;
        }
      }
      .ok {
        &:hover {
          background-color: #be943b;
          color: #222222;
        }
      }
    }
  }
  .linkPanel {
    .text,
    .url {
      display: flex;
      align-items: baseline;
      margin-bottom: 4px;
      input {
        width: 640px;
      }
      .label {
        width: 40px;
      }
    }
    .urlPrompt {
      margin-left: 42px;
      margin-top: 4px;
      display: flex;
      align-items: baseline;

      .goToURL {
        color: #888888;
        text-decoration: none;
        margin-right: 8px;
        &:hover {
          color: #cccccc;
          cursor: pointer;
        }
      }
      .rememberToWriteHead {
        color: #666666;
        font-size: 14px;
      }
    }
  }
  .metaDataPanel {
    .title {
      display: flex;
      align-items: baseline;
      margin-bottom: 8px;
      width: 420px;

      .label {
        width: 40px;
        margin-right: 12px;
      }
      .titleStuff {
        flex: 1;
        input {
          width: 100%;
          margin-bottom: 4px;
        }
        .exportFileName {
          margin-left: 2px;
          color: #666666;
          font-size: 14px;
          margin-bottom: 8px;
          word-break: break-all;
          .highlight {
            color: #888888;
          }
        }
      }
    }
    .id {
      display: flex;
      margin-bottom: 8px;
      .label {
        width: 40px;
        margin-right: 12px;
      }
      .idString {
        color: #666666;
        user-select: text;
      }
      ::selection {
        background-color: #c79d45;
        color: #222222;
      }
    }
    .toBottom {
      display: flex;
      align-items: baseline;
      margin-bottom: 8px;
      .toggle {
        width: fit-content;
        color: #aaaaaa;
        background-color: #2c2c2c;
        padding: 2px 6px;
        margin-left: 4px;
        &:hover {
          cursor: pointer;
          background-color: #242424;
          color: #cba044;
        }
      }
    }
    .baseURL {
      display: flex;
      align-items: baseline;
      width: 420px;
      input {
        flex: 1;
      }
      .label {
        width: fit-content;
        margin-right: 12px;
      }
    }
  }
  .chooseFilePanel {
    .files {
      width: 400px;
      max-height: 32vh;
      overflow-y: auto;
      color: #888888;
      background-color: #1a1a1a;
      box-sizing: border-box;
      padding: 4px;
      .fileBar {
        width: 100%;
        padding: 2px 8px;
        box-sizing: border-box;
        &:hover {
          background-color: #1f1f1f;
          cursor: pointer;
        }
      }
      .chosen {
        background-color: #be943b !important;
        color: #222222 !important;
        &:hover {
          cursor: default;
        }
      }
    }
  }
}
.input {
  outline: none;
  box-sizing: border-box;
  width: 200px;
  display: flex;
  background-color: transparent;
  font-family: "Segoe UI", "Microsoft YaHei UI", sans-serif;
  font-size: 16px;
  color: #999999;
  border: none;
  border-bottom: 2px #444444 solid;
  &::selection {
    background-color: #bbbbbb;
    color: #222222;
  }
  &::placeholder {
    color: #b62626;
  }
  &:focus {
    color: #cba044;
    border-color: #b08934;
    &::selection {
      background-color: #c79d45;
      color: #222222;
    }
  }
}
