.chatSeg {
  background-color: #333333;
  color: #bbbbbb;
  width: fit-content;
  padding: 4px 10px;
  border-radius: 4px;
  margin-bottom: 8px;
  max-width: 320px;
  overflow-wrap: break-word;
}
.leftSeg {
  background-color: #bbbbbb;
  color: #222222;
}
.rightSeg {
  align-self: end;
}
.centerSeg {
  align-self: center;
  color: #666666;
  margin-bottom: 8px;
  max-width: 360px;
}
.focused {
  background-color: #2e8c5f;
  color: #222222;
  border-radius: 4px;
  padding-left: 10px;
  padding-right: 10px;
}
